.container {
  display: flex;
  margin: 100px 50px 50px 50px;
}

.content {
  width: 156px;
  border: 1px solid black;
  margin: 0 auto;
}

.item {
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  display: inline-block;
  font-size: 25px;
  border: 1px solid black;
  cursor: pointer;
  vertical-align: middle;
  transition: background 0.1s;
}

.item:hover {
  background-color: #f1f1f10c;
}

.box {
  width: 100px;
  height: 100px;
  background-color: red;
}

.score-wrap {
  display: flex;
  justify-content: center;
  gap: 30px;
}

.scoreX,
.scoreO {
  font-family: 'Raleway';
  font-weight: 700;
  font-size: 26px;
  line-height: 1.19;
}

.player {
  display: block;
  font-family: 'Raleway';
  font-weight: 700;
  font-size: 26px;
  line-height: 1.19;
  text-align: center;
}
