.container {
  margin: 100px 50px 50px;
  display: flex;
}

.content {
  width: 156px;
  border: 1px solid #000;
  margin: 0 auto;
}

.item {
  width: 50px;
  height: 50px;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  border: 1px solid #000;
  font-size: 25px;
  line-height: 50px;
  transition: background .1s;
  display: inline-block;
}

.item:hover {
  background-color: rgba(241, 241, 241, .047);
}

.box {
  width: 100px;
  height: 100px;
  background-color: red;
}

.score-wrap {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.scoreX, .scoreO {
  font-family: Raleway;
  font-size: 26px;
  font-weight: 700;
  line-height: 1.19;
}

.player {
  text-align: center;
  font-family: Raleway;
  font-size: 26px;
  font-weight: 700;
  line-height: 1.19;
  display: block;
}

.toastify {
  color: #fff;
  opacity: 0;
  cursor: pointer;
  max-width: calc(50% - 20px);
  z-index: 2147483647;
  background: linear-gradient(135deg, #73a5ff, #5477f5);
  border-radius: 2px;
  padding: 12px 20px;
  text-decoration: none;
  transition: all .4s cubic-bezier(.215, .61, .355, 1);
  display: inline-block;
  position: fixed;
  box-shadow: 0 3px 6px -1px rgba(0, 0, 0, .12), 0 10px 36px -4px rgba(77, 96, 232, .3);
}

.toastify.on {
  opacity: 1;
}

.toast-close {
  color: #fff;
  cursor: pointer;
  opacity: .4;
  background: none;
  border: 0;
  padding: 0 5px;
  font-family: inherit;
  font-size: 1em;
}

.toastify-right {
  right: 15px;
}

.toastify-left {
  left: 15px;
}

.toastify-top {
  top: -150px;
}

.toastify-bottom {
  bottom: -150px;
}

.toastify-rounded {
  border-radius: 25px;
}

.toastify-avatar {
  width: 1.5em;
  height: 1.5em;
  border-radius: 2px;
  margin: -7px 5px;
}

.toastify-center {
  max-width: fit-content;
  max-width: -moz-fit-content;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
}

@media only screen and (max-width: 360px) {
  .toastify-right, .toastify-left {
    max-width: fit-content;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
  }
}

